const slider = () => {
    let slider = tns({
        "container": '.slider__wrapper',
        "controlsContainer": ".slider__controls",
        "items": 3,
        "slideBy": 1,
        "gutter": 10,
        "mouseDrag": true,
        "swipeAngle": false,
        "speed": 400,
        "nav": false
    });
    let sliderGuarantee = tns({
        "container": '.guarantee__slider',
        "controlsContainer": ".slider__controls-guarantee",
        "items": 1,
        "swipeAngle": false,
        "speed": 400,
        "mouseDrag": true,
    });
}
export default slider;